<template>
  <div class="vue-googlemap-container">
    <div class="vue-googlemap"></div>
    <slot></slot>
  </div>
</template>

<script>
import googlemapLoader, { instance } from "../googlemapLoader.js";
export default {
  name: "vue-googlemap",
  data() {
    return {};
  },
  props: {
    backgroundColor: String, // 
    center: {
      type: [Array, Object],
      required: true
    },
    clickableIcons: {
      // poi
      type: Boolean,
      default: true
    },
    disableDefaultUI: {
      // 
      type: Boolean,
      default: false
    },
    disableDoubleClickZoom: {
      // 
      type: Boolean,
      default: false
    },
    draggable: {
      // 
      type: Boolean,
      default: true
    },
    zoom: {
      type: Number,
      default: 13
    },
    maxZoom: Number,
    minZoom: Number,
    controls: {
      type: Array,
      default: () => [
        "zoom",
        "mapType",
        "scale",
        "streetView",
        "rotate",
        "fullscreen"
      ]
    },
    events: Object,
    mapOptions: Object
  },
  watch: {
    center: {
      handler (_new, _old) {
        if (_new !== _old) {
          if (this.$map === undefined) return
          console.log('112345')
          this.$map.setCenter(
            Array.isArray(nv) ? { lat: nv[0], lng: nv[1] } : nv
          );
          this.$map.panTo(
            Array.isArray(nv) ? { lat: nv[0], lng: nv[1] } : nv
          );
        }
      },
      deep: true,
      immediate: true
    }
  },
  mounted() {
    this.createMap();
  },
  destroyed() {
    google.maps.event.clearInstanceListeners(this.$map);
  },
  methods: {
    createMap() {
      const options = {};
      const _this = this;
      for (let key in this.$props) {
        if (this.$props[key] !== undefined) {
          if (key === "center") {
            const center = Array.isArray(this.$props[key])
              ? { lat: this.$props[key][0], lng: this.$props[key][1] }
              : this.$props[key];
            Object.assign(options, { center });
          } else if (key === "controls" && !this.$props["disableDefaultUI"]) {
            const controlsArray = [
              "zoom",
              "mapType",
              "scale",
              "streetView",
              "rotate",
              "fullscreen"
            ];
            controlsArray.forEach(c => {
              if (this.$props["controls"].indexOf(c) !== -1) {
                Object.assign(options, { [`${c}Control`]: true });
              } else {
                Object.assign(options, { [`${c}Control`]: false });
              }
            });
          } else {
            Object.assign(options, { [`${key}`]: this.$props[key] });
          }
        }
      }
      instance.load().then(() => {
        this.$map = new google.maps.Map(
          this.$el.querySelector(".vue-googlemap"),
          options
        );
        if (this.$children.length !== 0)
          this.$children.forEach(c => {
            c.$emit("mapReady", this.$map);
          });
        this.registerEvents();
        this.$watch("zoom", nv => {
          this.$map.setZoom(nv);
        });
        this.$watch("center", nv => {
          this.$map.setCenter(
            Array.isArray(nv) ? { lat: nv[0], lng: nv[1] } : nv
          );
          this.$map.panTo(
            Array.isArray(nv) ? { lat: nv[0], lng: nv[1] } : nv
          );
        });
        this.$emit('initMap', this.$map)
      });
    },
    setCenter (op) {
      this.$map.setCenter(op)
      this.$map.panTo(op)
    },
    getZoom () {
      return this.$map.getZoom()
    },
    getCenter () {
      return this.$map.getCenter()
    },
    registerEvents() {
      const events = this.events;
      const _this = this;
      if (!events) return;
      for (let eventName in events) {
        if (eventName) this.$map.addListener(eventName, function (e) {
          events[eventName](_this, _this.mapOptions, e)
        });
      }
    }
  }
};
</script>

<style>
.vue-googlemap-container {
  height: 100%;
}
.vue-googlemap {
  height: 100%;
}
</style>



